import React from 'react';
import { Form, FormGroup, Col, Button } from 'react-bootstrap';
import CodeEditor from './controls/CodeEditor';
import AlertDismissable from './controls/AlertDismissable';
import OutputBox from './controls/OutputBox';
import InputBox from './controls/InputBox';

import init, { run } from "../trav/pkg/trav.js";
const apiUrl = 'http://localhost:8000/';

const postData = async (data) => {
  const response = await fetch(apiUrl, {
    method: 'POST',
    mode: 'cors',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data)
  });
  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }
  
  const json = await response.json();
  return json;
};


class Editor extends React.Component {
  constructor(props) {
    super(props);

    console.log(`env: ${process.env.NODE_ENV}`);
    
    this.state = {
      task: {
        code: '',
        input: ''
      },
      response: {
        status: '0',
        message: '',
        c:0,
      },
    };

    this.handleRun = this.handleRun.bind(this);
    this.updateSolution = this.updateSolution.bind(this);
    this.handleCodeChange = this.handleCodeChange.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);

  }

  componentDidMount() {
    //CompilerApi.getTask('javascript')
      // .then(res => res.json())
      //.then((task) => {
       // console.log(task);
        //this.setState({ task });
      //});
  }

  handleCodeChange(code) {
    const { task } = this.state;
    task.code = code;
    console.log(code);
    console.log("new code");

    return this.setState({ task });
  }
  handleInputChange(input) {
    const { task } = this.state;
    task.input = input;
    console.log(input);
    console.log("new input");

    return this.setState({ task });
  }

  handleRun(event) {
    event.preventDefault();
    const { task } = this.state;
    console.log(task);
    let s1=task.code
    let s2=task.input
    init().then(() => {
      let r=run(s1,s2);
      console.log(r)
      const { response } = this.state;
      response.c=response.c+1
      response.message=String(r)+response.c.toString();
      this.setState({ response });
    })
    //postData({ input: s })
    //.then(data => {
    //const { response } = this.state;
    //response.c=response.c+1
    //console.log(data);
    //response.message=String(data.output)+response.c.toString();
    //this.setState({ response });
    //})
  .catch(error => {
    console.error(error);
  });
  }

  updateSolution(event) {
    // event.preventDefault();
    console.log(this.state.task);
    const field = event.target.name;
    const { task } = this.state;
    task[field] = event.target.value;
    return this.setState({ task });
  }

  render() {
    return (
      <div className="container">
         <Form horizontal>
          <FormGroup controlId="code">
            <Col sm={12}>
              <CodeEditor onChange={this.handleCodeChange} code={this.state.task.code} />
            </Col>
          </FormGroup>
          <FormGroup>
            <Col sm={12}>
              <InputBox onChange={this.handleInputChange} input={this.state.task.input} />
            </Col>
          </FormGroup>
          <FormGroup>
            <Col sm={2}>
              <Button bsStyle="primary" type="button" onClick={this.handleRun}>
                Run
              </Button>
              
            </Col>
            <Col sm={10} />
          </FormGroup>
          <FormGroup>
            <Col sm={12}>
              <AlertDismissable
                show={this.state.response.status !== '0'}
                message={this.state.response.message}
              />
            </Col>
          </FormGroup>
          <FormGroup>
            <Col sm={12}>
              <OutputBox
                show={this.state.response.status === '0'}
                message={this.state.response.message}
              />
            </Col>
          </FormGroup>
        </Form>
      </div>
    );
  }
}

export default Editor;
